<template>
<button class="btn btn-create">
	<slot></slot>
</button>
</template>

<script>
export default {
	name: 'ButButton',
	props: {
		size: {
			value: String,
			default: ''
		},
		value: {
			value: String,
			default: ''
		}
	},
	methods: {
		input() {
			this.$emit('input');
		}
	}
}
</script>

<style scoped>
.btn-create {
    height: auto;
    line-height: auto;
    padding: 4px 24px;
	background-color: #4c6575;
}

.btn{
	/* 布局 */
	margin: 0 6px;
	padding: 0 2rem;
    height: 28px;
    line-height: 28px;
	vertical-align: middle;
	text-decoration: none;
	text-align: center;
	/* text-transform: uppercase; */
	
	/* 样式 */
	border: 0;
	border-radius: 2px;
	cursor: pointer;
	outline: none;
	color: #fff;
	box-shadow: 0 2px 3px 0 rgba(0,0,0,.16), 0 2px 5px 0 rgba(0,0,0,.12);
    
    letter-spacing: .5px;

    display: inline-block;
    -webkit-tap-highlight-color: transparent;
	transition: .2s ease-out;
}
.btn:hover {
	box-shadow: 0 3px 8px 0 rgba(0,0,0,.18), 0 4px 15px 0 rgba(0,0,0,.15);
}
.btn:active {
	transition: .1s ease-out;
	opacity: 0.5;
}
.btn:disabled {
	pointer-events: none;
	cursor: default;
	opacity: 0.5;
}
</style>